      this.hotChart = echarts.init(document.getElementById("hot-words-echart"));
      const option = {
        series: [
          {
            type: "wordCloud",
            sizeRange: [12, 40],
            rotationRange: [-90, 90],
            rotationStep: 45,
            gridSize: 15,
            shape: "pentagon",
            width: "100%",
            height: "100%",
            drawOutOfBound: true,
            textStyle: {
              color: function () {
                return (
                  "rgb(" +
                  [
                    Math.round(Math.random() * 160),
                    Math.round(Math.random() * 160),
                    Math.round(Math.random() * 160),
                  ].join(",") +
                  ")"
                );
              },
            },
            emphasis: {
              textStyle: {
                shadowBlur: 10,
                shadowColor: "#333",
                color: "red",
              },
            },
            data: this.keyWords,
          },
        ],
      };
      this.hotChart.setOption(option);
    },
    handleHotTrend(item, index) {
      this.trendType.forEach((subItem) => {
        subItem.active = false;
      });
      item.active = true;
      this.hotTrendActive = index;
