<template>
  <div id="line-trend-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      // 
      optionsBar: {
        xAxis: {
          type: "category",
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: [820, 932, 901, 934, 1290, 1330, 1320],
            type: "line",
            smooth: true,
            areaStyle: {},
          },
        ],
      },
    };
  },
  mounted() {
    this.initEchart();
  },
  methods: {
    initEchart() {
      const lineTrendChart = document.querySelector("#line-trend-charts");
      const myChart = echarts.init(lineTrendChart);
      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#line-trend-charts {
  width: 100%;
  height: 220px;
}
</style>