<template>
  <div id="pie-type-charts"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  props: {
    pieData: {
      type: Array,
      default: () => [
        3901, 1934, 4290, 3330, 5320, 932, 3901, 1934, 4290, 3330, 5320, 320,
        932, 3901, 1934, 4290, 3330, 5320,
      ],
    },
  },
  data() {
    return {
      // 
      optionsPie: null,
      data: [
        123, 234, 345, 456, 567, 587, 654, 678, 785, 795, 812, 835, 856, 877,
        944, 988, 1010, 1050, 1060, 1420, 1456,
      ],
      pieTimer: null,
    };
  },
  watch: {
    pieData: function (newValue, oldValue) {
      this.initEchart();
    },
  },
  mounted() {
    this.initEchart();
    this.pieTimer = setInterval(() => {
      this.data = this.data.map((item) => item + 20);
      this.initEchart();
    }, 3000);
  },
  methods: {
    initEchart() {
      const pieTypeChart = document.querySelector("#pie-type-charts");
      if (!pieTypeChart) {
        clearInterval(this.pieTimer);
        return;
      }
      this.optionsPie = {
        grid: [
          {
            left: "15%",
            top: "50",
            right: "15%",
            bottom: "20",
          },
        ],
        xAxis: {
          type: "category",
          data: [
            "00:00",
            "01:15",
            "02:30",
            "03:45",
            "05:00",
            "06:15",
            "07:30",
            "08:45",
            "10:00",
            "11:15",
            "12:30",
            "13:45",
            "15:00",
            "16:15",
            "17:30",
            "18:45",
            "20:00",
            "21:15",
            "22:30",
            "23:45",
            "24:00",
          ],
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)",
            },
          },
          axisLabel: {
            color: "#fff",
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.05)"],
              width: 1,
              type: "solid",
            },
          },
        },
        series: [
          {
            symbol: "circle",
            symbolSize: 5,
            name: "",
            lineStyle: {
              color: "#5E8FFD",
              width: 1,
            },
            data: this.data,
            type: "line",
            smooth: true,
            areaStyle: {
              color: "rgba(16,138,238,0.20)",
            },
          },
        ],
      };
      const myChart = echarts.init(pieTypeChart);
      myChart.setOption(this.optionsPie);
    },
  },
};
</script>

<style lang="scss" scoped>
#pie-type-charts {
  width: 100%;
  height: 220px;
}
</style>